.MainContainer{
    width: 100%;
    padding-top: 10%;
}
.Title{
    font-size: 2em;
    font-style: italic;
    text-align: right;
}
.ContentContainer{
    width: 100%;
    height: 100%;
}

@media (min-width:800px){
    .MainContainer{
        width: 60%;
        padding: 5% 0 0 20%;
    }
    .Title{
        font-size: 2.5em;
    }
    .ContentContainer{
        padding-top: 10%;
    }
}


