
.Header{
    width: 100%;
    position: fixed;
}
.Header ul{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: right;
    background-color:  mediumseagreen;
}
.Header li{
    display: inline-block;
}

.Header a{
    display: block;
    padding: 10px;
    text-decoration: none;
    color: white;
}
.Header a:hover,
.Header a.myActive{
    display: block;
    border-radius: 5px;
    background-color:  white;
    color: mediumseagreen;
}

.Logo{
    left: 0;
    position: fixed;
}

@media(max-width:700px){
    .Header{
        display: none;
    }
}


