.AboutContainer{
    width: 100%;
    height: 60%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: white;
}

img{
    width: 20%;
    height: 60%;
    display: block;
    margin: 0;
    padding: 0;

}

.Content{
    width: 70%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
    padding-left: 5%;

    -webkit-transition-property: width display justify-content align-items padding-left;

    -o-transition-property: width display justify-content align-items padding-left;

    transition-property: width display justify-content align-items padding-left;
    -webkit-transition-timing-function: ease-in-out;
         -o-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
    -webkit-transition-duration: 1s;
         -o-transition-duration: 1s;
            transition-duration: 1s;
    background-color: mediumseagreen;
}
.Intro{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    -webkit-transition-delay: 2s;
         -o-transition-delay: 2s;
            transition-delay: 2s;
    margin: 0;
    padding: 0;
}
.Intro > p{
    margin: 5px;
    padding: 0;
    text-align: justify;
}
.Skills{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    padding-top: 5%;
}
.Skills > section{
    width: 100%;
}
.Skills > p{
    -ms-flex-positive:1;
        flex-grow:1;
    width: 33%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin: 0;
    padding-top: 10px;
}


